<template>
	<view class="my-page-root">
		<my-nav-bar title="帖子详情"/>
		<view class="my-page-root-child">
			<view>{{post.title}}</view>
			<view>{{post.number}}</view>
			<view v-if="post.series">
				<text>系列：</text>
				<button @click="findSameSeries">{{post.series}}</button>
			</view>
			<view>{{post.content}}</view>
			<image v-if="post.picSrc" mode="aspectFit" style="max-width: 90%" :src="post.picSrc"/>
			
			<!--如果浏览的是自己的，则可以做删除操作-->
			<button v-if="isLogin&&post.userId==userId" @click="isDelete=true">删除</button>
			<u-modal v-model="isDelete"
			:title-style="{color: 'cornflowerblue'}"
			@confirm="confirmDelete"
			@cancel="isDelete=false"
			:show-cancel-button="true">
				<text>确定删除？</text>
			</u-modal>
			
			<view>创建时间：{{post.startTime}}</view>
			<view>{{post.finished?"已结束":"进行中..."}}</view>
			<view>限票：{{post.maxVoteCount>0?post.maxVoteCount:"不限制"}}</view>
			<view>截止日期：{{post.endTime?post.endTime:"无期限"}}</view>
			<view>浏览次数：{{post.viewCount}}</view>
			
			<view v-if="!isLogin">
				<text>尚未登录，投票需先登录</text>
			</view>
			<view v-if="isLogin">
				<button @click="collect">{{hasCollected?"已收藏":"未收藏"}}</button>
				
				<text>{{voteOptionId?"您已选投了 ["+getVoteOptionDesc()+"]":"未参与选投"}}</text>
			</view>
			<button @click="showComments">评论({{post.commentCount}})</button>
			
			<!--结果展示-->
			<button @click="showChart=!showChart">{{showChart?"隐藏结果":"展示结果"}}</button>
			<button v-if="showChart" @click="changeChart">切换视图</button>

			<!--展示图-->
			<view class="my-center-one" v-if="loaded&&showChart">
				<view v-if="chartType=='bar'" style="width: 90%;">
					<my-bar-chart :options="myBarChartData"></my-bar-chart>
				</view>
				<view v-if="chartType=='pie'" style="width: 90%;">
					 <qiun-data-charts 
					      type="pie"
					      :opts="pieChartData.opts"
					      :chartData="pieChartData.chartData"
					    />
				</view>
			</view>
			
			<!--选择列表-->
			<view>
				<view v-for="option,index in post.options" :key="index">
					<my-option-show :option="option"/>
					<button v-if="isLogin&&!voteOptionId" @click="()=>{voteFor(index)}">选投</button>
				</view>
			</view>
			
			<!--弹出层-->
			<u-popup v-model="isShowComments" mode="bottom" height="80%">
				<view>
					<view>评论</view>
					<button @click="orderComments">{{"当前："+(orderByTimeOrHot=="hot"?"按热度排序":"按时间排序")}}</button>
					<input v-model="inputComment"/><button @click="addComment">发送</button>
					
					<view v-if="comments.length==0">
						空
					</view>
					<view v-for="cmt,index in comments" :key="cmt.id">
						<my-comment :comment="cmt"/>
						<view v-if="isLogin">
							<!--不知为何，这里多传入一个cmt才正确-->
							<button @click="()=>{toReply(index,cmt)}">回复</button>
							<button v-if="userId==cmt.userId" @click="()=>{isDeleteComment=true;targetCommentIndex=index}">删除</button>
						</view>
					</view>
					
					<u-modal v-model="isReply"
					:title-style="{color: 'cornflowerblue'}"
					@confirm="confirmReply"
					@cancel="isReply=false"
					:show-cancel-button="true">
						<view class="slot-content">
							<my-comment :comment="comments[targetCommentIndex]"/>
							<input v-model="commentForReply">
						</view>
					</u-modal>
					
					<u-modal v-model="isDeleteComment"
					:title-style="{color: 'cornflowerblue'}"
					@confirm="confirmDeleteComment"
					@cancel="isDeleteComment=false"
					:show-cancel-button="true">
						<text>确定删除？</text>
					</u-modal>
					
					<my-pagination @searchPage="searchPage" ref="mypagination"/>
				</view>
			</u-popup>

		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				post:{
					id:"",
					title:"",
					content:"",
					picSrc:"",
					category:"",
					number:"",
					key:"",
					startTime:"",
					endTime:"",
					viewCount:0,
					userId:1,
					commentAble:true,
					maxVoteCount:1,
					voteCount:0,//当前已投票数
					series:"",
					options:[],
					finished:false,
					commentCount:0,
				},

				voteOptionId: null,//用户已经投过的选项id，没有投过则为null
				showChart: false,//是否显示图形，如果voteOptionId为空，则默认不显示
				chartType:'bar',//柱形'bar'或者扇形fan
				loaded:false,//加载完毕再允许显示图形，主要针对自己的组件bug

				//自己做的条形图所需数据
				myBarChartData:[
					{desc:"",count:0},
				],
				//引入的扇形图所需数据
				pieChartData:{
					chartData:{
						series: [
						    {//扇形图有一定的局限，如果选项描述过长，可能显示不友好，自己没法调整，只有让帖子建立者自行调整选项长度
								data: [
									{name:"",value:0,labelText:"自定义"},
								]
						    }
						]
					},
					opts: {//对显示图形的细节调整
						extra: {
						  pie: {
							border: true,
							borderWidth: 1,
							borderColor: "white",
							//..其他属性暂时不需要
						  },
						}
					},
				},
				
				isDelete:false,//正要删除
				hasCollected:false,//用户是否已收藏过
				isLogin: false ,//用户是否已登录
				userId:null,//登录的用户id
				
				isShowComments:false,
				comments:[],
				pageSize:3,
				orderByTimeOrHot:"hot",
				inputComment:"",
				isReply:false,
				commentForReply:"",
				isDeleteComment:false,
				targetCommentIndex:0,
			};
		},
		methods:{
			changeChart(){
				this.chartType=this.chartType=='bar'?'pie':'bar'
			},
			voteFor(index){
				const optionId = this.post.options[index].id
				this.loaded=false
				this.api.post.vote(this.post.id,optionId,()=>{
					
					this.util.toast("投选成功")
					
					this.post.voteCount+=1
					this.voteOptionId=optionId
					
					this.post.options[index].count+=1
					this.refreshChartDataByPostOptions()

					this.loaded=true
					this.showChart=true
				})
			},
			getVoteOptionDesc(){
				for(const option of this.post.options){
					if(option.id==this.voteOptionId)
						return option.desc
				}
				return "空"
			},
			refreshChartDataByPostOptions(){
				
				this.myBarChartData=[...this.post.options]
				
				let pirArray=[]
				for(const option of this.post.options){
					let rate
					if(this.post.voteCount==0)
						rate = Math.floor(100*1/this.post.options.length)+"% "
					else rate = Math.floor(100*option.count/this.post.voteCount)+"% "
					
					pirArray.push({
						name:option.desc,
						value:option.count,
						labelText:rate+option.desc
					})
				}
				this.pieChartData.chartData.series[0].data=[...pirArray]
			},
			collect(){
				if(!this.hasCollected){
					this.api.user.addCollect(this.post.id,(data)=>{
						this.util.toast("收藏成功",800)
						this.hasCollected=true
					})
				}
				else{
					this.api.user.removeCollect(this.post.id,(data)=>{
						this.util.toast("取消了收藏",500)
						this.hasCollected=false
					})
				}
			},
			findSameSeries(){
				this.util.routeTo("SameSeriesPosts",
				{
					userId:this.post.userId,
					series:this.post.series,
				})
			},
			
			confirmDelete(){
				this.api.post.delete(this.post.id,()=>{
					this.util.routeBack()
					this.util.toast("已删除！")
				})
			},
			
			load(){
				//post的id和key会被预先从路由参数里获取并赋值
				this.loaded=false
				this.api.post.getById(this.post.id,this.post.key,(data)=>{
					this.post=data
					this.refreshChartDataByPostOptions()
					this.loaded=true
				},(msg)=>{
					this.util.routeBack()
					this.util.toast(msg?msg:"请求失败")
				})
			},
			backRefresh(){//该函数自动在使用util.routeBack到该页面后被调用
				this.load()
			},
			
			
			searchPage({targetPage,newStart}){
				this.api.comment.findPostComments(targetPage,this.pageSize,this.post.id,this.orderByTimeOrHot,(data)=>{
					const succ = this.util.dealPage(targetPage,this.pageSize,data.total)
					if(succ){
						if(newStart)
							this.comments=data.comments
						else
							this.comments=[...this.comments,...data.comments]
					}
				})
			},
			toPage1(){
				if(this.$refs.mypagination)
					this.$refs.mypagination.reset()
			},
			showComments(){
				this.toPage1()
				this.isShowComments=true
			},
			orderComments(){
				this.orderByTimeOrHot=this.orderByTimeOrHot=="hot"?"time":"hot"
				this.showComments()
			},
			addComment(){
				if(this.inputComment.length==0){
					this.util.toast("评论不能为空")
					return
				}
				
				const dto={
					id:null,
					postId:this.post.id,
					remark:this.inputComment
				}
				
				this.api.comment.add(dto,(data)=>{
					this.util.toast("成功添加一条评论")
					this.post.commentCount+=1
					this.showComments()
				})
			},
			confirmDeleteComment(){
				this.api.comment.delete(this.comments[this.targetCommentIndex].id,(succ)=>{
					this.util.toast("已删除")
					this.post.commentCount-=1
					this.showComments()
				})
			},
			toReply(index,cmt){
				this.isReply=true;
				this.targetCommentIndex=index;
			},
			confirmReply(){
				if(this.commentForReply.length==0){
					this.util.toast("回复不能为空")
					return
				}
				
				const dto={
					id:this.comments[this.targetCommentIndex].id,
					postId:this.post.id,
					remark:this.commentForReply
				}
				
				this.api.comment.reply(dto,(data)=>{
					this.util.toast("回复成功")
					this.post.commentCount+=1
					this.showComments()
				})
			}
		},
		onLoad(routeParamObj){
			const {id,key} = routeParamObj
			this.post.id=id
			this.post.key=key//这个是安全的，通过一般搜索无法搜索到带key的，只有搜自己的会自动附上key
			this.load()
			
			this.isLogin = this.vuex.get("isLogin")
			if(this.isLogin){
				this.userId=this.vuex.get("user").id
			}
			
			if(this.isLogin){
				this.api.post.hasVoted(id,(data)=>{
					this.voteOptionId = data.optionId
					if(this.voteOptionId)
						this.showChart=true
					else this.showChart=false
				})
				
				this.api.user.hasCollected(id,(data)=>{
					this.hasCollected=data
				})
			}
			else this.showChart=false
		}
	}
</script>

<style lang="scss" scoped>


</style>
